import React, { Component } from 'react';
import { connect } from 'react-redux';
import { Link } from 'react-router-dom';
import axios from 'axios';
import { currentArticleLoaded } from '../../redux/actions/actionCreators';
import { makeHeadersForAuth } from '../../utils/utils';
import PersonalArea from '../personalArea/personalArea.jsx';

class Article extends Component {
  getArticleFromServer = () => {
    const { slug } = this.props.match.params;
    const headers = makeHeadersForAuth();
    axios
      .get(`https://conduit.productionready.io/api/articles/${slug}`, { headers })
      .then((response) => {
        this.props.currentArticleLoaded(response.data.article);
      });
  };

  setLike = async () => {
    const { slug } = this.props.match.params;
    const headers = makeHeadersForAuth();
    await axios
      .post(`https://conduit.productionready.io/api/articles/${slug}/favorite`, null, { headers })
      .then((response) => {
        this.props.currentArticleLoaded(response.data.article);
      });
  };

  componentDidMount() {
    this.getArticleFromServer();
  }

  setUnlike = async () => {
    const { slug } = this.props.match.params;
    const headers = makeHeadersForAuth();
    await axios
      .delete(`https://conduit.productionready.io/api/articles/${slug}/favorite`, {
        headers,
      })
      .then((response) => {
        this.props.currentArticleLoaded(response.data.article);
      });
  };

  toggleLike = () => {
    const { currentArticle } = this.props;
    const { favorited } = currentArticle;
    if (!favorited) {
      this.setLike();
    } else {
      this.setUnlike();
    }
  };

  render() {
    const { slug } = this.props.match.params;
    const { currentArticle = { author: { username: null } } } = this.props;
    const linkToEdit = `/blog-platform/articles/${slug}/edit`;
    const { title, author, body, favorited, favoritesCount } = currentArticle;
    return (
      <>
        <br />

        <div style={{ marginTop: '15px' }}>
          <div style={{ display: 'flex', marginBottom: '15px' }}>
            <PersonalArea />

            <div
              style={{
                // display: 'flex',
                maxWidth: '1600px',
                // flexWrap: 'wrap',

                minHeight: '800px',
                border: '1px solid gray',
                borderRadius: '10px',
                boxShadow: '0 0 6px 0 #34495e',
                padding: '12px',
                minWidth: '1000px',
                margin: '5px',
                marginRight: '15px',
              }}
            >
              <div>
                <Link to={linkToEdit}>Редактировать</Link>
              </div>
              <div>
                <br />
                <h3>title: {title}</h3>
                <span>author: {author.username}</span>
                <br />
                <br />
                <span>body: {body}</span>
                <br />
                <br />
                <span>slug: {slug}</span>
                <br />
                <br />
                <span>Всего лайков: {favoritesCount}</span>
                <br />
                <br />
                <span>
                  <button onClick={this.toggleLike}>
                    {favorited ? 'Снять' : 'Поставить'} лайк
                  </button>
                </span>
              </div>
            </div>
          </div>
        </div>
      </>
    );
  }
}

function mapStateToProps(state) {
  return {
    currentArticle: state.currentArticle,
  };
}

const mapDispatchToProps = (dispatch) => {
  return {
    currentArticleLoaded: (currentArticle) => dispatch(currentArticleLoaded(currentArticle)),
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(Article);
