import {
  ARTICLES_LOADED,
  CURRENT_ARTICLE_LOADED,
  AUTHORIZED,
  SHOW_ALL,
  SHOW_MY,
  CURRENT_USER_PROFILE_LOAD,
} from '../actions/actionTypes';

const initialState = {
  articles: [],
  currentArticle: {
    title: null,
    author: { username: null },
    body: null,
    favorited: null,
    favoritesCount: null,
  },
  articlesCount: 0,
  autorized: false,
  show: 'all',
  currentUser: { image: '"https://static.productionready.io/images/smiley-cyrus.jpg' },
};

export default function rootReducer(state = initialState, action) {
  const { type, articles, articlesCount, currentArticle, autorized, currentUser } = action;
  switch (type) {
    case ARTICLES_LOADED:
      return Object.assign({}, state, { articles, articlesCount });
    case CURRENT_ARTICLE_LOADED:
      return Object.assign({}, state, { currentArticle });
    case AUTHORIZED:
      return Object.assign({}, state, { autorized });
    case SHOW_ALL:
      return Object.assign({}, state, { show: 'all' });
    case SHOW_MY:
      return Object.assign({}, state, { show: 'my' });
    case CURRENT_USER_PROFILE_LOAD:
      return Object.assign({}, state, { currentUser });
    default:
      return state;
  }
}
